import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { FaSortDown } from 'react-icons/fa'
import { FaTimes } from 'react-icons/fa'
import { FaPencilAlt } from 'react-icons/fa'
import { Link } from 'react-router-dom'
import { Consumer } from '../../Context'
import axios from 'axios'



 class Contact extends Component {
     state = {
         showContactInfo: false
     }

   onShowClick = (e) => {
       this.setState({showContactInfo: !this.state.showContactInfo})
   }

    onDeleteClick = async (id, dispatch) => {
       await axios.delete(`https://jsonplaceholder.typicode.com/users/${id}`)
        .then(res => dispatch({type:'DELETE_CONTACT', payload: id}))
    }

    render() {
        const {id, name, email, phone} = this.props.contact
        const { showContactInfo } = this.state
    
        return (
            <Consumer>
                {value => {
                    const { dispatch } = value
                    return(
                        <div className='card card-body mb-3'>
                        <h4>{name}<FaSortDown style={iconStyle} onClick={this.onShowClick} />
                        <FaTimes style ={{cursor:'pointer', float:'right', color:'red'}} onClick ={this.onDeleteClick.bind(this, id, dispatch)} />
                        <Link to={`contacts/edit/${id}`}>
                        <FaPencilAlt style={{cursor:'pointer', float:'right', color:'black', marginRight:'1rem'}} />
                        </Link>
                        </h4>
                        { showContactInfo ? (<ul className='list-group'>
                            <li className='list-group-item'>Email: {email}</li>
                            <li className='list-group-item'>Phone: {phone}</li>
                        </ul>) : null}
                        
                    </div>
                    )
                }}
            </Consumer>
        )
    }
}

Contact.propTypes = {
    contact: PropTypes.object.isRequired
}

const iconStyle = {
    cursor: 'pointer'
}

export default Contact;