import React from 'react';
import PropTypes from 'prop-types'
import {Link} from 'react-router-dom'
import { FaHome } from 'react-icons/fa'
import { FaPlus } from 'react-icons/fa'
import { FaQuestion } from 'react-icons/fa'


 const Header = (props) => {
     const { branding } = props
    return (
        <nav className='navbar navbar-expand-sm navbar-dark bg-primary mb-3 py-0'>
            <div className="container">
                <Link to="/" className="navbar-brand">{branding}</Link>
                <ul className='navbar-nav ml-auto'>
                    <li className="nav-item">
                       <Link to="/" className="nav-link"><FaHome /> Home</Link>
                    </li>
                    <li className="nav-item">
                        <Link to="/contacts/add" className="nav-link"><FaPlus /> AddContacts</Link>
                    </li>
                    <li className="nav-item">
                        <Link to="/about" className="nav-link"><FaQuestion /> About</Link>
                    </li>
                </ul>
            </div>
        </nav>
    )
}

Header.defaultProps = {
    branding: 'My App'
}

Header.propTypes = {
    branding: PropTypes.string.isRequired
}

export default Header;